<template>
  <div class="douyin-page" @click.stop="toggleVideoPlay">
    <div class="title">{{ data.resourceName || '无标题视频' }}</div>

    <template v-if="showVideo">
      <video
        ref="videoRef"
        :src="data.sourceUrl"
        :poster="data.resourceImage"
        muted
        preload="auto"
        loop
        @play="onVideoPlay"
        @pause="onVideoPause"
        @timeupdate="onVideoTimeupdate"
      />
    </template>
    <img v-else :src="data.resourceImage" alt="" class="poster" />

    <transition v-if="controlVisible" name="fade">
      <div class="control" @click="toggleVideoPlay"></div>
    </transition>

    <div v-if="process > 0" class="process">
      <span :style="{ left: `${process}%` }"></span>
    </div>

    <div class="info">
      <img :src="data.authorImage" alt="" />
      <div>
        <div class="author">{{ data.author || '无作者' }}</div>
        <div class="desc">
          <span>{{ data.authorHospitalName }}</span>
          <span v-if="data.authorDepartment"> | {{ data.authorDepartment }}</span>
          <span v-if="data.authorJobTitle"> | {{ data.authorJobTitle }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    data: Object,
    showVideo: Boolean
  },
  data() {
    return {
      controlVisible: false,

      process: -1
    }
  },
  mounted() {
    if (this.showVideo) {
      this.$nextTick(() => {
        this.$refs.videoRef.play()
      })
    }
  },
  methods: {
    onVideoPlay() {
      this.controlVisible = false
    },
    onVideoPause() {
      this.controlVisible = true
    },
    onVideoTimeupdate() {
      const { currentTime, duration } = this.$refs.videoRef
      if (currentTime && duration) {
        this.process = (currentTime / duration).toFixed(2)
      }
    },
    toggleVideoPlay() {
      console.log('toggleVideoPlay')
      const $video = this.$refs.videoRef
      if ($video.paused) {
        $video.play()
        this.controlVisible = false
      } else {
        $video.pause()
        this.controlVisible = true
      }
    }
  }
}
</script>

