<template>
  <div id="app">
    <div class="player">
      <!-- capture prevent stop-->
      <div
        class="container"
        @touchstart.stop="handleTouchStart"
        @touchmove.stop="handleTouchMove"
        @touchend.stop="handleTouchEnd"
      >
        <div class="refresh" :style="{ top: `${refreshTop}px` }">
          <span v-if="refreshLoading">加载中...</span>
          <span v-else>{{ refreshTop > refreshKeyPoint ? '下拉' : '松开' }}刷新</span>
        </div>
        <div ref="pageBox" :style="{ transform: `translateY(${translateY}px)` }">
          <douyin-page
            v-for="(item, index) in playList"
            :key="item.id"
            :data="item"
            :show-video="index === activeIndex"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DouyinPage from '@/components/DouyinPage.vue'
import videoData from '@/util/video-data.js'

export default {
  name: 'App',
  components: { DouyinPage },
  data() {
    return {
      playList: videoData,
      activeIndex: 0,
      prevVideo: null,
      activeVideo: null,
      nextVideo: null,

      translateY: 0,

      // 刷新相关
      refreshKeyPoint: 70,
      refreshTop: 0,
      refreshLoading: false
    }
  },
  created() {
    this.initVideoIns()
  },
  mounted() {
    this.$pageBox = this.$refs.pageBox
  },
  methods: {
    initVideoIns() {
      if (this.activeIndex - 1 >= 0) {
        this.prevVideo = this.playList[this.activeIndex - 1]
      } else {
        this.prevVideo = null
      }

      this.activeVideo = this.playList[this.activeIndex]

      if (this.activeIndex + 1 <= this.playList.length - 1) {
        this.nextVideo = this.playList[this.activeIndex + 1]
      } else {
        this.nextVideo = null
      }
    },

    handleTouchStart(e) {
      this.touchStratTime = Date.now()
      this.touchStartY = e.changedTouches[0].clientY
    },

    handleTouchMove(e) {
      const offsetY = e.touches[0].clientY - this.touchStartY
      this.translateY = -this.activeIndex * window.innerHeight + offsetY

      if (this.activeIndex === 0) {
        this.refreshTop = offsetY >= this.refreshKeyPoint ? this.refreshKeyPoint : offsetY
      }
    },

    handleTouchEnd(e) {
      const offsetTime = Date.now() - this.touchStratTime
      const offsetY = e.changedTouches[0].clientY - this.touchStartY

      const speed = Math.abs(offsetY / offsetTime)

      // 速度和距离任一达到条件视为拖动成功
      if (speed > 1 || Math.abs(offsetY) > window.innerHeight / 4) {
        if (offsetY > 0) {
          this.moveToIndex(this.activeIndex - 1)
        } else {
          this.moveToIndex(this.activeIndex + 1)
        }
      } else {
        this.moveToIndex(this.activeIndex)
      }

      if (offsetY >= this.refreshKeyPoint && this.activeIndex === 0) {
        this.doRefresh()
      }
    },

    moveToIndex(newIndex) {
      let index = newIndex
      if (newIndex < 0) {
        index = 0
      }

      if (newIndex > this.playList.length - 1) {
        index = this.playList.length - 1
        this.toast('没有了')
      }

      const translateY = -index * window.innerHeight
      this.$pageBox.style.transitionDuration = '.3s'
      this.$pageBox.style.transitionProperty = 'transform'
      this.$pageBox.style.transitionTimingFunction = 'easy'
      this.translateY = translateY

      this.$pageBox.addEventListener('transitionend', () => {
        this.$pageBox.style.transitionDuration = '0s'
        this.activeIndex = index

        // document.querySelector('video').play()
      })
    },

    doRefresh() {
      this.refreshLoading = true
      const timer = setTimeout(() => {
        this.refreshLoading = false
        this.refreshTop = 0
        clearTimeout(timer)
      }, 1000)
    },

    toast(msg) {
      const el = document.createElement('div')
      el.classList.add('toast')
      el.innerText = msg

      document.body.appendChild(el)
      const timer = setTimeout(() => {
        el.remove()
        clearTimeout(timer)
      }, 1000)
    }
  }
}
</script>
